nav{
    display: flex;
    border-bottom: 2px solid #f3f3f3;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    z-index: 999;
    margin-left: 2rem;
}

input {
    padding: 12px 20px;
    border: none;
    background: #f7f6f6;
    outline: none;
    border-radius: 5px;
    margin-right: 20px;
    width: 14rem;
    position: relative;
}

.nav-icons{
    color: rgb(59, 57, 57);
    width: 1.5rem;
    height: 1.4rem;
    margin-left: 2rem;
}